<script>
  export let enabled;
</script>

<span class="sliderb">
  <label class="switch">
    <input type="checkbox" bind:checked={enabled} />
    <span class="slider" />
  </label></span
>

<style>
  .switch {
    position: relative;
    display: inline-block;
    width: 65px;
    height: 39px;
    margin: 4px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 30px;
    width: 30px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  input:checked + .slider {
    background-color: #2b349f;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2b349f;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  .sliderb {
    width: 100%;
    box-sizing: border-box;
    border: none;
    display: block;
  }
</style>
